import Vue from 'vue'
import Vuex from 'vuex'
Vue.use(Vuex)
let store = new Vuex.Store({
  state: {
    num: 'vuex测试',
    as: '111',
    user: '',
    zan1: 0,
    zan2: 0
  },
  mutations: {
    as (state, a) {
      state.as = a
      console.log(state.as)
    },
    deng (state, a) {
      state.user = a
    },
    zans1 (state) {
      state.zan1++
    },
    zans2 (state) {
      state.zan2++
    }
  },
  getters: {},
  actions: {}
})
export default store



