<template>
  <div class="one">
    <div class="tops">
      <div>登录</div>
      <div>国学论坛</div>
      <div>搜索</div>
    </div>
    <div class="list">
      <div>经典书籍</div>
      <div>诗词文学</div>
      <div>琴棋书画</div>
      <div>育儿启蒙</div>
    </div>
  </div>
</template>
<script>
export default {
  name: 'one',
  data () {
    return {}
  },
  mounted () {},
  methods: {}
}
</script>
<style scoped lang="scss">
.tops{
  width: 100%;
  height: 40px;
  background: #961105;
  border: 5px solid #B67262;
  border-left: 0;
  border-right: 0;
  display: flex;
  justify-content: space-around;
  align-items: center;
  div{
    width: 30%;
  }
  div:nth-child(1) {
    color: white;
    text-align: left;
  }
  div:nth-child(2) {
    color: white;
    text-align: center;
  }
  div:nth-child(3) {
    color: white;
    text-align: right;

  }
}
.list{
  width: 100%;
  height: 40px;
  display: flex;
  justify-content: space-around;
  align-items: center;
  background: white;
}
</style>



