<template>
  <div class="index">
    手机号<input type="text" v-model="user.w1">
    <br>
    密码<input type="password" v-model="user.w2">
    <div @click="deng">登录</div>
  </div>
</template>
<script>
export default {
  name: 'index',
  data () {
    return {
      user: {
        w1: '',
        w2: ''
      }
    }
  },
  mounted () {},
  methods: {
    deng () {
      this.$store.commit('deng', this.user)
      this.$router.push({
        name: 'list2'
      })
      console.log('vuex', this.$store.state.user)
    }
  }
}
</script>
<style scoped lang="scss">

</style>



