<template>
  <div class="homes">
    <!-- top -->
    <ones></ones>
    <!--  -->
    <div class="nav">
      <mt-button size="small" :class="{'active':active=='tab-container1'}" @click.native.prevent="active = 'tab-container1'">全部</mt-button>
      <mt-button size="small" :class="{'active':active=='tab-container2'}" @click.native.prevent="active = 'tab-container2'">新帖</mt-button>
      <mt-button size="small" :class="{'active':active=='tab-container3'}" @click.native.prevent="active = 'tab-container3'">热门</mt-button>
      <mt-button size="small" :class="{'active':active=='tab-container4'}" @click.native.prevent="active = 'tab-container4'">精华</mt-button>
    </div>
    <div class="page-tab-container">
      <mt-tab-container class="page-tabbar-tab-container" v-model="active" swipeable>
        <mt-tab-container-item id="tab-container1">
          <div class="nums1">
            <img src="@/../img/cers.png" alt="">
            <div style="color: red;">国学论坛APP下载~</div>
            <div></div>
          </div>
          <div class="nums1">
            <div>
              <span>顶</span>
            </div>
            <div>主题售价和附件售价赚取国学币教程</div>
            <div>admin</div>
          </div>
          <div class="nums1">
            <div>
              <span>顶</span>
            </div>
            <div>国学论坛APP下载~</div>
            <div>admin</div>
          </div>
          <div class="nums2">更多</div>
          <!--  -->
          <div class="user">
            <div class="a1">
              <img src="@/../img/cers.png" alt="">
              yonghu
            </div>
            <div class="a2">
              我的世界我的世界我的世界我的世界我的世界我的世界我的世界
            </div>
            <div class="a3">
              <div>评论0  |</div>
              <div @click="zan1">  点赞{{this.$store.state.zan1}}</div>
            </div>
          </div>
          <div class="user">
            <div class="a1">
              <img src="@/../img/cers.png" alt="">
              yonghu
            </div>
            <div class="a2">
              我的世界我的世界我的世界我的世界我的世界我的世界我的世界
            </div>
            <div class="a3">
              <div>评论0  |</div>
              <div @click="zan2">  点赞{{this.$store.state.zan2}}</div>
            </div>
          </div>
        </mt-tab-container-item>
        <mt-tab-container-item id="tab-container2">
          2
        </mt-tab-container-item>
        <mt-tab-container-item id="tab-container3">
          3
        </mt-tab-container-item>
        <mt-tab-container-item id="tab-container4">
          4
        </mt-tab-container-item>
      </mt-tab-container>
    </div>
    <!--  -->
    <two></two>
  </div>
</template>
<script>
export default {
  name: 'homes',
  data () {
    return {
      active: 'tab-container1'
    }
  },
  mounted () {},
  methods: {
    zan1 () {
      if (this.$store.state.user.w1) {
        this.$store.commit('zans1')
      } else {
        this.$messagebox.confirm('您没有登录，是否登录').then(action => {
          this.$router.push({
            name: 'index'
          })
        })
      }
    },
    zan2 () {
      if (this.$store.state.user.w1) {
        this.$store.commit('zans2')
      } else {
        this.$messagebox.confirm('您没有登录，是否登录').then(action => {
          this.$router.push({
            name: 'index'
          })
        })
      }
    }
  }
}
</script>
<style scoped lang="scss">
.nav{
  width: 100%;
  height: 40px;
  display: flex;
  justify-content: space-around;
  align-items: center;
  .mint-button--small{
    border-radius: 0;
    width: 25%;
    border: 0;
    outline: none;
    background: white;
    box-shadow: 0 !important;
    border-bottom: 1px solid white;
  }
  .mint-button--default{
    box-shadow: 0 0 0px;
  }
  .active{
    border-bottom: 1px solid red;
  }
  .mint-button::after{
    background: white;
  }
}
.nums1{
  width: 100%;
  height: 30px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-bottom: 1px solid #ccc;
  img{
    width: 30px;
    height: 30px;
    margin:0 2%;
  }
  div{
    font-size: 14px;
  }
  div:nth-child(1){
    width: 30px;
    height: 30px;
    margin: 0 2%;
    span{
      display: block;
      width: 20px;
      height: 20px;
      background: red;
      margin: 5px;
      text-align: center;
      line-height: 20px;
      color:white;
      border-radius: 2px;
    }
  }
  div:nth-child(2){
    text-align: left;
    width: 80%;
  }
  div:nth-child(3){
    width: 10%;
    margin-right: 2%;
    color: #ccc;
  }
}
.nums2{
  width: 100%;
  height: 30px;
  text-align: center;
  line-height: 30px;
  border-bottom: 1px solid #ccc;
  font-size: 14px;
}
.user{
  width: 100%;
  img{
    width: 30px;
    height: 30px;
    margin: 0 2%;
    background: white;
    margin-top: 20px solid red;
  }
  .a1{
    display: flex;
    justify-content: flex-start;
    align-items: center;
    color: black !important;
  }
  .a2{
    margin: 0 2%;
    width: 96%;
  }
  .a3{
    margin:0 2%;
    width: 96%;
    height: 40px;
    display: flex;
    justify-content: flex-start;
    align-items: center;
  }
}
</style>



