<template>
  <div class="home">
    <!-- top -->
    <one></one>
    <!-- swiper -->
    <mt-swipe :show-indicators="true">
      <mt-swipe-item>
        <img src="@/../img/pic1.png" alt="">
      </mt-swipe-item>
      <mt-swipe-item>
        <img src="@/../img/pic2.png" alt="">
      </mt-swipe-item>
      <mt-swipe-item>
        <img src="@/../img/pic3.png" alt="">
      </mt-swipe-item>
    </mt-swipe>
    <!--  -->
    <div class="gundong">
      <div>
        <img src="@/../img/tb.png" alt="">
        国学论坛APP下载~</div>
      <div>2019-06-07</div>
    </div>
    <!-- 推荐 -->
    <div class="tj_top">
      <div>板块推荐</div>
      <div>更多</div>
    </div>
    <div class="tj_list">
      <div>
        <img src="@/../img/icon1.png" alt="">
        积分兑换
      </div>
      <div @click='tiao'>
        <img src="@/../img/icon2.png" alt="">
        高士讲堂
      </div>
      <div>
        <img src="@/../img/icon3.png" alt="">
        历史交流
      </div>
      <div>
        <img src="@/../img/icon4.png" alt="">
        经文研究
      </div>
      <div>
        <img src="@/../img/icon1.png" alt="">
        儒家经典
      </div>
      <div>
        <img src="@/../img/icon2.png" alt="">
        教导文化
      </div>
      <div>
        <img src="@/../img/icon3.png" alt="">
        医家学派
      </div>
      <div>
        <img src="@/../img/icon4.png" alt="">
        兵家学派
      </div>
    </div>
    <!--  -->
    <div class="tjh">
      <div>今日推荐</div>
      <div>《吕氏春秋》</div>
    </div>
    <!-- bottom -->
    <two></two>
  </div>
</template>
<script>
export default {
  name: 'home',
  data () {
    return {}
  },
  mounted () {},
  methods: {
    tiao () {
      this.$store.commit('as', '高士讲堂')
      this.$router.push({
        name: 'list2'
      })
    }
  }
}
</script>
<style scoped lang="scss">
.home{
  width: 100%;
  height: 100%;
  background: #F5F5F5;
}
.mint-swipe{
  width: 100%;
  height: 200px;
  img{
    width: 100%;
    height: 100%;
    display: block;
  }
  .mint-swipe-indicators{
    display: none !important;
  }
}
.gundong{
  background: white;
  width: 100%;
  height: 30px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  color: #7C7C7C;
  div:first-child{
    display: flex;
    justify-content: flex-start;
    align-items: center;
    margin-left: 2%;
    img{
      width: 20px;
      height: 20px;
    }
  }
  div:last-child{
    margin-right: 2%;
  }
}
.tj_top{
  background: white;
  width: 100%;
  height: 30px;
  margin-top: 20px;
  border-bottom: 1px solid #f5f5f5;
  display: flex;
  justify-content: space-between;
  align-items: center;
  div:nth-child(1){
    font-weight: bold;
    margin-left: 2%;
  }
  div:nth-child(2){
    margin-right: 2%;
    font-size: 12px;
    color: #ccc;
  }
}
.tj_list{
  background: white;
  width: 100%;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  align-items: center;
  div{
    width: 25%;
    height: 80px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    font-size: 12px;
    img{
      width: 40px;
      height: 40px;
    }
  }
}
.tjh{
  background: white;
  width: 100;
  height: 30px;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  border: 1px solid #ccc;
  border-left: 0;
  border-right: 0;
  div:nth-child(1){
    background: red;
    color: white;
    font-size: 12px;
    margin: 0 2%;
    padding: 2px;
  }
  div:nth-child(2){
    color: #727272;
  }
}
</style>



