
<template>
  <div class="one">
    <div class="tops">
      <div @click="go">返回</div>
      <div>{{as}}</div>
      <div>
        <img src="@/../img/cers.png" alt="">
      </div>
    </div>
  </div>
</template>
<script>
export default {
  name: 'one',
  data () {
    return {
      as: this.$store.state.as
    }
  },
  mounted () {},
  methods: {
    go () {
      this.$router.go(-1)
    }
  }
}
</script>
<style scoped lang="scss">
.tops{
  width: 100%;
  height: 40px;
  background: #961105;
  border: 5px solid #B67262;
  border-left: 0;
  border-right: 0;
  display: flex;
  justify-content: space-around;
  align-items: center;
  div{
    width: 30%;
  }
  div:nth-child(1) {
    color: white;
    text-align: left;
  }
  div:nth-child(2) {
    color: white;
    text-align: center;
  }
  div:nth-child(3) {
    color: white;
    text-align: right;
    img{
      width: 30px;
      height: 30px;
    }
  }
}
</style>



