<template>
  <div class="one">
    <div class="btms">
      <div>
        <img src="@/../img/tb.png" alt="">
        聚焦
      </div>
      <div>
        <img src="@/../img/tb.png" alt="">
        社会
      </div>
      <div class="cer">
        <div class="cersn">
          <img src="@/../img/cers.png" alt="">
        </div>
      </div>
      <div>
        <img src="@/../img/tb.png" alt="">
        高士
      </div>
      <div>
        <img src="@/../img/tb.png" alt="">
        我的
      </div>
    </div>
  </div>
</template>
<script>
export default {
  name: 'one',
  data () {
    return {}
  },
  mounted () {},
  methods: {}
}
</script>
<style scoped lang="scss">
.btms{
  background: white;
  width: 100%;
  height: 50px;
  display: flex;
  justify-content: space-around;
  align-items: center;
  position: fixed;
  left: 0;
  bottom: 0;
  border-top: 1px solid #7e7e7e;
  div{
    width: 20%;
    height: 50px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    font-size: 12px;
    img{
      width: 30px;
      height: 30px;
    }
  }
  .cer{
    width: 70px;
    height: 70px;
    border: 1px solid #7e7e7e;
    border-radius: 50%;
    background: white;
    .cersn{
      width: 55px;
      height: 55px;
      background: #F75D5B;
      border-radius: 50%;
    }
  }
}
</style>



