<template>
  <div class="hello">
    <h1>{{ msg }}</h1>
    <h2>Essential Links</h2>
    <div class="aaa">
      sass测试1
      <span>sass测试2</span>
      <div>{{$store.state.num}}</div>
    </div>
  </div>
</template>
<script>
import { Toast } from 'mint-ui'
export default {
  name: 'HelloWorld',
  data () {
    return {
      msg: 'Welcome to Your Vue.js App'
    }
  },
  mounted () {
    Toast({
      message: '已经安装mintui',
      duration: 2000
    })
    var params = {
      code: 'utf-8',
      q: '内衣',
      callback: 'cb'
    }
    this.$http.post('/api2',
      JSON.stringify(params)
    ).then(function (res) {
      console.log('axios测试', res)
    })
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
h1, h2 {
  font-weight: normal;
}
.aaa{
  color: red;
  span{
    color: green;
  }
}
</style>



